.container {
    max-width: 100%;
    display: flex;
    justify-content: center;
    background-color: #f0eded;
    height: 70px;
}

.con {
    width: 100%;
}

.setup-container {
    height: 60px;
    /* border: 2px solid black; */
    display: flex;
    justify-content: space-evenly;
    background-color: white;
}

.setup-logo {
    margin-top: 10px;
}

.logo {
    height: 30px;
    max-width: 100%;
}

.setup-serach {
    margin-top: 10px;
    width: 43.5%;
}

.set-input {
    font-size: 17px;
    background-color: #f0f5ff;
}

.setup-navbar {
    display: flex;
    margin-top: 19px;
}

.setup-icon1 {
    margin-right: 6px;
}

.icon1 {
    height: 22px;
    max-width: 100%;
}

.name {
    margin: 0;
    font-size: 16px;
    font-family: sans-serif;
}

.setup-navbar2 {
    margin-top: 5px;
    display: flex;
}

div#dropdownDelay {
    width: 20%;
}

.setup-icon2 {
    margin-right: 6px;
    height: 30px;
}

.setDropDown {
    padding-bottom: 5px;
    font-size: 15px;
    font-family: sans-serif;
}

button#dropdownDelayButton {
    font-weight: 400;
    font-family: sans-serif;
}

.dropDownHover {
    margin: 0px;
}

.ml-2\.5 {
    margin-left: 5px;
}

.seticon3 {
    display: flex;
    margin-top: 20px;
}

.icon3 {
    margin-right: 5px;
}

.btn1 {
    font-family: sans-serif;
}

.setup-navbar4 {
    margin-top: 10px;
}

.dropDownHover1 {
    width: 23%;
    border-radius: 0px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.dropDownHover2 {
    width: 15%;
    border-radius: 0px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.set-box {
    margin-top: 5px;
}

.last {
    width: 15%;
}

.main1 {
    display: flex;
    justify-content: space-between;
}

.li-btn {
    padding-right: 4px;
    font-size: 16px;
}

.font1 {
    font-size: 16px;
}

.setup-li {
    display: flex;
}

.noti {
    font-size: 16px;
    margin-left: 10px;
}

.noti2 {
    font-size: 16px;
    margin-left: 10px;
}

.noti3 {
    font-size: 16px;
    margin-left: 10px;
}

.noti4 {
    font-size: 16px;
    margin-left: 10px;
}

.s {
    padding-right: 5px;
}

/* Complete Navbar Css */

/* Media Queries */


@media screen and (min-width: 200px) and (max-width : 1000px) {

    .name {
        display: none;
    }

    .s {
        display: none;
    }

    .btn1 {
        display: none;
    }


}


@media screen and (min-width: 200px) and (max-width : 800px) {

    .container {
        max-width: 100%;
        background-color: white;

    }

    .con {
        width: 100%;
        /* display: none; */
    }

    .setup-logo {
        margin-top: 10px;
        display: flex;
        justify-content: center;
    }

    .setup-container {
        /* height: 70px; */
        /* border: 2px solid black;  */
        display: block;
        background-color: white;
    }

    .logo {
        height: 55px;
    }

    .setup-navbar {
        display: none;
    }


    .setup-icon1 {
        display: none;
    }

    .setup-name {
        display: none;
    }

    .setup-navbar2 {
        display: none;
    }

    .setup-navbar3 {
        display: none;
    }

    .setup-navbar4 {
        display: none;
    }

    .setup-serach {
        margin-top: 10px;
        width: 100%;
    }

}