.footer-container {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

.first-footer-container {
    margin-top: 50px;
    width: 98%;
    background-color: rgb(255, 255, 255);
}

.footer1 {
    font-family: sans-serif;
    font-size: 16px;
    font-weight: 600;
    padding-bottom: 20px;
    color: #2f2323;
    padding-top: 30px;
}

.footer2 {
    font-size: 14px;
    font-family: sans-serif;
    color: #2f2323;
}

.span1 {
    font-family: sans-serif;
    font-size: 15px;
    font-weight: 400;
}

.footer3h1 {
    font-family: sans-serif;
    font-size: 18px;
    color: #958f8f;
    padding-top: 50px;
    padding-left: 20px;
    font-weight: 600;
}

.footer3p1 {
    font-family: sans-serif;
    font-size: 12px;
    color: #958f8f;
    padding-left: 20px;
    margin: 0px;

}

.set {
    padding-top: 10px;
}

.color-footer {
    background-color: #292929;
    height: 358px;
}

.lastfooter {
    display: flex;
    margin-bottom: 30px;
}

.div1 {
    height: 250px;
    width: 180px;
    margin-top: 20px;

}

.setdiv2 {
    border-left: 1px solid #747474;
    padding-left: 10px;
    width: 230px;
}

.setdiv3 {
    border: none;
    padding-left: 0px;
}

.all-headings {
    color: #a2a2a2;
    padding-top: 30px;
    padding-left: 20px;
    font-size: 13px;
}

.all-items {
    color: white;
    padding-left: 20px;
    margin: 0px;
    font-size: 12px;


}

.footerlast {
    border: 1px solid #5b5b5b;

}

.end-footer {
    display: flex;
    justify-content: space-around;
}

.footer-item1 {
    padding: 20px 0px;
    color: white;
    padding-bottom: 0px;
    font-size: 13px;
    display: flex;
}

.set-last-item {
    position: relative;
    top: -9px;
    padding-right: 10px;
}


/* Media Queries */


@media screen and (min-width: 800px) and (max-width : 1000px) {

    .div1 {
        height: 250px;
        width: 125px;
        /* margin-top: 20px; */
    }

    .setdiv2 {
        display: none;
    }

    .setdiv3 {
        display: none;
    }

    .footer-item1 {
        font-size: 9px;
    }

    .set-last-item {
        padding-right: 2px;
    }


}

@media screen and (min-width: 200px) and (max-width : 800px) {

    .color-footer {
        background-color: #292929;
        height: 518px;
    }

    .setdiv2 {
        border-left: 1px solid #747474;
        padding-left: 10px;
        width: 230px;
        display: none;
    }


    .end-footer {
        display: block;
        margin-top: 20px;

    }


    .footer-item1 {
        padding: 0px 0px;
        color: white;
        padding-bottom: 0px;
        font-size: 13px;
        display: flex;
    }

}


@media screen and (min-width: 200px) and (max-width : 800px) {

    .media {
        display: none;
    }



}