

/* card css */

.container1 {
    width: 100%;
    background-color: #f0eded;
    display: flex;
    height: 262px;
    justify-content: center;
}

.color{
    background-color: #f0eded;

}

.center{
    display: flex;
    justify-content: center; 
}
div#root {
    background-color: #f0eded;
}

.flex1{
    display: flex;
    overflow-y: hidden;
    width: 84.9%;
    background-color: white;
    margin-bottom: 5px;
}

.set-item1 {
    height: 250px;
    width: 100%;
    padding-top: 20px;
    /* padding-left: 15px;  */
    padding-right: 15px;
}

.setitem2{
    padding-left: 13px; 
}

::-webkit-scrollbar {
    width: 6px; 
    height: 6px;
    
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #555; /* Change the color on hover */
  }

  .seth1 {
    font-size: 18px;
    /* display: inline; */
    font-family: sans-serif;
    margin-left: 8px;
    width: 98.7%;
    background-color: white;
    margin-bottom: 0px;
    padding-left: 10px;
    padding-top: 10px;
    font-weight: 600;
}
 

.divitem {
    height: 263px;
    width: auto;
    position: relative;
    top: -6px;
    left: 0px;
}


@media screen and (min-width: 200px) and (max-width : 800px){

    .seth1 {
        font-size: 17px;
        /* display: inline; */
        font-family: sans-serif;
        margin-left: 8px;
        width: 100%;
        background-color: white;
        margin-bottom: 0px;
        padding-left: 10px;
        padding-top: 10px;
        font-weight: 600;
    }

    .container1 {
        width: 100%;
        /* background-color: #f0eded; */
        display: flex;
        height: 195px;
        justify-content: center;
    }


    .set-item1 {
        height: 180px;
        width: 100%;
        padding-top: 20px;
        /* padding-left: 15px; */
        padding-right: 15px;
    }

    .divitem {
        height: 200px;
        width: auto;
        position: relative;
        top: -10px;
        left: 0px;
    }





}

